import { Box, Button, Grommet, Text } from "grommet";
import { Edit, Trash } from "grommet-icons";
import React from "react";

const theme = {
    global: {
        font: {
            family: 'Prosto One',
        },
    },
    card: {
        container: {
            background: '#1a3a39',
            elevation: 'none',
        },
        footer: {
            pad: { horizontal: 'medium', vertical: 'small' },
        },
    },
    heading: {
        color: '#ADFF0037',
        level: {
            2: {
                medium: {
                    size: "30px",
                    height: "40px",
                }
            }
        }
    },
};

export type MessageProps = {
    id: any;
    title: string;
    createdAt: string;
    authorUserName: string;
    // deleteMessage: () => void
    // editMessage: (newTitle: string) => void
};

export const MessageComponent: (props: MessageProps) => JSX.Element = (
    props: MessageProps
) => {
    return (<Grommet theme={theme}>
        <Box margin={{ vertical: "small", horizontal: "medium" }} border={{ color: "#E9FFE1" }} round="small">
            <Box direction="row">
                <Text size="medium" margin={{ horizontal: "small" }} color="#ADFF00">{props.authorUserName}</Text>
                <Button icon={<Edit />} focusIndicator={false} hoverIndicator onClick={() => { /*setOpenEd(true)*/ }} />
                <Button icon={<Trash />} focusIndicator={false} hoverIndicator onClick={() => { /*setOpenDel(true)*/ }} />
            </Box>
            <Text size="small" margin={{ horizontal: "small" }}>{props.title}</Text>
        </Box>
    </Grommet>)
}